"use client"

import { useState } from "react"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Search, MoreVertical } from "lucide-react"

const mockCustomers = [
  { id: 1, name: "Amit Kumar", upiId: "amit@upi", stamps: 12, visits: 15, lastVisit: "2 days ago" },
  { id: 2, name: "Priya Singh", upiId: "priya@upi", stamps: 8, visits: 10, lastVisit: "1 day ago" },
  { id: 3, name: "Vikram Patel", upiId: "vikram@upi", stamps: 5, visits: 6, lastVisit: "3 days ago" },
  { id: 4, name: "Neha Sharma", upiId: "neha@upi", stamps: 20, visits: 25, lastVisit: "Today" },
]

export default function CustomersTable() {
  const [search, setSearch] = useState("")

  const filtered = mockCustomers.filter(
    (c) =>
      c.name.toLowerCase().includes(search.toLowerCase()) ||
      c.upiId.toLowerCase().includes(search.toLowerCase())
  )

  return (
    <div className="space-y-2">
      {/* Compact Search */}
      <div className="relative max-w-xs">
        <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
        <Input
          placeholder="Search customers"
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="h-9 pl-8 text-sm"
        />
      </div>

      {/* Table */}
      <div className="rounded-md border">
        <Table className="card border-0">
          <TableHeader>
            <TableRow className="bg-muted/40">
              <TableHead className="w-[240px]">Customer</TableHead>
              <TableHead>UPI</TableHead>
              <TableHead className="text-center">Stamps</TableHead>
              <TableHead className="text-center">Visits</TableHead>
              <TableHead>Last Visit</TableHead>
              <TableHead className="w-[40px]" />
            </TableRow>
          </TableHeader>

          <TableBody>
            {filtered.map((c) => (
              <TableRow key={c.id} className="hover:bg-muted/30">
                {/* Customer */}
                <TableCell>
                  <div className="flex items-center gap-2">
                    <div className="h-7 w-7 rounded-full bg-primary/10 text-primary flex items-center justify-center text-xs font-semibold">
                      {c.name[0]}
                    </div>
                    <span className="font-medium">{c.name}</span>
                  </div>
                </TableCell>

                {/* UPI */}
                <TableCell className="text-muted-foreground">
                  {c.upiId}
                </TableCell>

                {/* Stamps */}
                <TableCell className="text-center">
                  <span className="inline-flex h-6 min-w-[24px] items-center justify-center rounded-full bg-primary text-white text-xs font-medium">
                    {c.stamps}
                  </span>
                </TableCell>

                {/* Visits */}
                <TableCell className="text-center">
                  {c.visits}
                </TableCell>

                {/* Last Visit */}
                <TableCell className="text-muted-foreground">
                  {c.lastVisit}
                </TableCell>

                {/* Actions */}
                <TableCell className="text-right">
                  <Button variant="ghost" size="icon" className="h-8 w-8">
                    <MoreVertical size={16} />
                  </Button>
                </TableCell>
              </TableRow>
            ))}

            {filtered.length === 0 && (
              <TableRow>
                <TableCell
                  colSpan={6}
                  className="h-24 text-center text-sm text-muted-foreground"
                >
                  No customers found
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </div>
    </div>
  )
}
